.App {
  text-align: center;
  
}

a {
  color: #125d98;
  text-decoration: none;
}

/* header component  */

.header {
  color: #125d98;
  background-color: #dddddd;
  margin: 0%
}

.headerText {
  font-family: "Chicle", cursive;
  font-size: 4em;
  margin: 0%;
  padding-bottom: 2.5% 0%;
}

/* quote component  */

.swanson {
  background-color: #dddddd;
  padding-bottom: 5%;
  height: 90vh;
  background-image: url('images/swanson.png');
  background-repeat: no-repeat;
  padding-top: 15%;
}

.swansonImg {
  position: relative;
  height: 300px;
  width: auto;
}

.quote {
  color: #125d98;
  margin: 0% 15% 5%;
  padding: 15% 5%;
  border: 3px solid #3c8dad;
  border-radius: 25px;
  background-color: #f5a962;
  font-family: "Chicle", cursive;
  font-size: 1.2em;
  padding-top: 10%;
}

.refreshBtn {
  color: #125d98;
  background-color: #f5a962;
  border: 3px solid #3c8dad;
  border-radius: 8px;
  padding: 2.5%;
}

.refreshBtn {
  transition-duration: 0.4s;
}

.refreshBtn:hover {
  background-color: #3c8dad;
  border: 3px solid #f5a962;
  color: white;
}

/* footer component  */
 .footer {
  font-family: "Chicle", cursive;
  color: #125d98;
  background-color: #dddddd;
  font-size: 1.5em;
  margin: 0%;
  padding-top: 2.5%;
 }

 .footer p {
   margin: 0%;
 }

 .social-icon {
   margin: 0% 2% 2% 2%;
   border: 2px solid #fff;
   border-radius: 50%;
 }

 .social-icon:hover {
   background-color: #fff
 }

/* responsive css */
@media screen and (min-width: 1000px) {
  .swanson {
    padding-top: 5%;
  }
  .quote {
    margin: 0% 30% 2%;
    padding: 5%;
  }
  .swansonImg {
    height: 200px;
  }
  .refreshBtn {
    margin-top: 1%;
  }
}
